const baseUrl = 'https://api.rawg.io/api/games?key=f1e220559d054b338d52a897c59a2743';
const baseDetailUrl = 'https://api.rawg.io/api/games/'
const apiKey = '?key=f1e220559d054b338d52a897c59a2743'


const getCurrentMonth = () => {
  const month = new Date().getMonth() + 1
  if(month < 10){
    return(`0${month}`)
  }else{
    return(month)
  }
}
const getCurrentDay = () => {
  const day = new Date().getDate() 
  if(day < 10){
    return(`0${day}`)
  }else{
    return(day)
  }
}


const currentDay = getCurrentDay()
const currentMonth = getCurrentMonth()
const currentYear = new Date().getFullYear()

const currentDate = `${currentYear}-${currentMonth}-${currentDay}`
const lastDate = `${currentYear-1}-${currentMonth}-${currentDay}`
const nextDate = `${currentYear+1}-${currentMonth}-${currentDay}`

const popularGames = `&dates=${lastDate},${currentDate}&ordering=-rating&page_size=9`
const upcomingGames = `&dates=${currentDate},${nextDate}&ordering=-released&page_size=9`
const newGames = `&dates=${currentDate}&ordering=-added&page_size=9`

export const popularGamesUrl = () => `${baseUrl}${popularGames}`
export const upcomingGamesUrl = () => `${baseUrl}${upcomingGames}`
export const newGamesUrl = () => `${baseUrl}${newGames}`
export const gameDetailsUrl = (id) => `${baseDetailUrl}${id}${apiKey}`
export const screenshotUrl = (id) => `${baseDetailUrl}${id}/screenshots${apiKey}`
export const searchGamesUrl = (game_name) => `${baseUrl}&search=${game_name}&page_size=9`



