import React, { useEffect } from "react";
import { useDispatch, useSelector } from 'react-redux';
import styled from "styled-components";
import { loadGames } from "../actions/gameActions";
import { motion,AnimatePresence,AnimateSharedLayout} from "framer-motion";
import Game from "../components/games";
import GameDetail from "../components/gameDetail";
import { useLocation } from "react-router";


const Home = () => {
  const location = useLocation();
  const pathId = location.pathname.split('/')[2];
  const dispatch = useDispatch();
  useEffect(() => {
    dispatch(loadGames())
  }, [dispatch]);
  const { popular, upcoming, newGames, searched } = useSelector(state => state.games)
  return (
    <>
    <AnimateSharedLayout type='crossfade'>
    <AnimatePresence>{pathId && <GameDetail LayoutId={pathId}/>}</AnimatePresence>

    {searched.length ?(
    <div>
    <GameList className='searched'>
      <h2>Searched Games</h2>
      <Games>
        {
          searched.map(game => (
            <Game name={game.name} released={game.released} image={game.background_image} id={game.id} key={game.id}/>
          ))
        }
      </Games>
    </GameList>
    </div>
    ):''}

    <GameList>
    
      <h2>Popular Games</h2>
      <Games>
        {
          popular.map(game => (
            <Game name={game.name} released={game.released} image={game.background_image} id={game.id} key={game.id}/>
          ))
        }
      </Games>
      
    </GameList>

    <GameList>
      <h2>Upcoming Games</h2>
      <Games>
        {
          upcoming.map(game => (
            <Game name={game.name} released={game.released} image={game.background_image} id={game.id} key={game.id}/>
          ))
        }
      </Games>
    </GameList>

    <GameList>
    <h2>New Games</h2>
    <Games>
      {
        newGames.map(game => (
          <Game name={game.name} released={game.released} image={game.background_image} id={game.id} key={game.id}/>
        ))
      }
    </Games>
    </GameList>
    </AnimateSharedLayout>
    </>
  )
}

const GameList = styled(motion.div)`
  padding:0rem 5rem;
  overflow: hidden;
`
const Games = styled(motion.div)`
  padding:3rem 0rem;
  display: grid;
  grid-template-columns: repeat(auto-fit,minmax(300px,1fr));
  grid-column-gap: 2rem;
  grid-row-gap: 3rem;
`

export default Home;