import React from "react";
import styled from "styled-components";
import { motion } from "framer-motion";
import { useSelector } from 'react-redux';
import { useHistory } from 'react-router-dom';
import { smallImage } from "../util";
import apple from '../img/apple.svg'
import gamepad from '../img/gamepad.svg'
import nintendo from '../img/nintendo.svg'
import playstation from '../img/playstation.svg'
import steam from '../img/steam.svg'
import xbox from '../img/xbox.svg'
import starEmpty from '../img/star-empty.png'
import starFull from '../img/star-full.png'



const GameDetail = ({ pathId }) => {
  const history = useHistory();
  const exitDetailHandler = (e) => {
    const element = e.target;
    if (element.classList.contains('shadow')) {
      document.body.style.overflow = 'auto';
      history.push('/');
    }
  };

  const getPlatform = (platform) => {
    switch (platform) {
      case 'PlayStation 5':
        return playstation;
      case 'PlayStation 4':
        return playstation;
      case 'PlayStation 3':
        return playstation;
      case 'Xbox One':
        return xbox;
      case 'Xbox 360':
        return xbox;
      case 'PC':
        return steam;
      case 'Nintendo Switch':
        return nintendo;
      case 'iOS':
        return apple;
      default:
        return gamepad;
    }
  };

  const getStars = () => {
    const stars = []
    for(let i=1; i<=5;i++){
      if(i<=detail.rating){
        stars.push(<img alt={i} src={starFull} key={i}></img>)
      }
      else{
        stars.push(<img alt={i} src={starEmpty} key={i}></img>)
      }
    }
    return stars;
  }

  const { detail, screenshot, isLoading } = useSelector((state) => state.game);
  return (
    <>
      {!isLoading && (
        <CardShadow className='shadow' onClick={exitDetailHandler}>
          <Detail LayoutId={pathId}>
            <Stats>
              <div className="rating">
                <h3>{detail.name}</h3>
                <p>Rating: {detail.rating}</p>
                {getStars()}
              </div>
              <Info>
                <h3>Platforms</h3>
                <Platforms>
                  {detail.platforms.map(data => (
                    <img key={data.platform.id} src={getPlatform(data.platform.name)} alt='data.platform.name'></img>
                  ))}
                </Platforms>
              </Info>
            </Stats>
            <Media>
              <img src={smallImage(detail.background_image, 1280)} alt={detail.background_image} />
            </Media>
            <Desc>
              {detail.description_raw}
            </Desc>
            <div className="gallery">
              {screenshot.results.map(screen => (
                <img src={smallImage(screen.image, 1280)} key={screen.id} alt={screen.image} />
              ))}
            </div>
          </Detail>
        </CardShadow>
      )}
    </>
  );
}

const CardShadow = styled(motion.div)`
  width:100%;
  min-height:100vh;
  overflow-y: scroll;
  position:fixed;
  left:0;
  top:0;
  background: rgba(0,0,0,0.5);
  overflow-x: hidden;
`
const Detail = styled(motion.div)`
  width:80%;
  min-height: 100vh;
  position: absolute;
  background: white;
  border-radius: 1rem;
  padding:2rem 5rem;
  color:black;
  left:10%;
  img{
    width:100%;
  }
`
const Stats = styled(motion.div)`
  display: flex;
  align-items: center;
  justify-content: space-between;
  img{
    height:1rem;
    width:1rem;
    display: inline;
  }
`
const Info = styled(motion.div)`
  text-align: center;
`
const Platforms = styled(motion.div)`
  display:flex;
  justify-content: space-evenly;
  img{
    margin-left:2rem; 
    height:1.5rem;
    width:1.5rem;
  }
`
const Media = styled(motion.div)`
  margin-top:3rem;
  img{
    width:100%;
  }
`
const Desc = styled(motion.div)`
  margin:1rem 0rem;
  text-align:center;
`

export default GameDetail;