const initialState = {detail:{platforms:[]},screenshot:{results:[]},isLoading:true}

export const detailReducer = (state=initialState,action) => {
  switch(action.type){
    case 'GAME_DETAIL':
      return{
        ...state,
        detail: action.payload.game,
        screenshot:action.payload.screen,
        isLoading: false
      };
    case 'LOADING_DETAIL':
      return{
        ...state,
        isLoading: true
      };
    default:
      return{
        ...state
      }
  }
}